
.guide main {
  background: #fff;
  max-height: 70vh;
  overflow: auto;
  margin-bottom: 20px;
  padding: 0 10px 10px;
  border-top: 2px solid #ccc;
  border-bottom: 2px solid #ccc;
}
.guide .note {
  font-size: smaller;
}
.guide .note::before {
    content: "Note:";
    font-weight: bold;
    text-transform: uppercase;
}
.bounce-enter-active {
  -webkit-animation: bounce-in .5s;
          animation: bounce-in .5s;
}
.bounce-leave-active {
  -webkit-animation: bounce-in .5s reverse;
          animation: bounce-in .5s reverse;
}
@-webkit-keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
ul li {
  margin-bottom: 10px;
}
code {
  display: block;
  white-space: pre-line;
}



