
.interactivity {
  background: #ccc;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 10px;
}
.interactivity > div {
    display: block;
}
.interactivity > div:first-of-type {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
}
.interactivity .btn.change-page,
  .interactivity .btn.show-preferences,
  .interactivity .btn.reload-doc,
  .interactivity .btn.export,
  .interactivity .btn.preview,
  .interactivity .exported {
    display: inline-block;
    margin-left: 10px;
}
.interactivity .user-delete .btn {
    margin-left: 5px;
}
.user-update {
  position: fixed;
  top: 50px;
  left: 0;
  right: 0;
  background: #eee;
  padding: 5px 10px;
}
.slide-enter-active {
  -webkit-animation: slide-in .3s;
          animation: slide-in .3s;
}
.slide-leave-active {
  -webkit-animation: slide-in .3s reverse;
          animation: slide-in .3s reverse;
}
@-webkit-keyframes slide-in {
0% {
    max-height: 0;
    opacity: 0;
}
100% {
    max-height: 63px;
    opacity: 100%;
}
}
@keyframes slide-in {
0% {
    max-height: 0;
    opacity: 0;
}
100% {
    max-height: 63px;
    opacity: 100%;
}
}



